.lists {
	text-align: center;
	&__inner {
		padding-top: 80px;
		padding-bottom: 80px;
		@include md {
			padding-top: 50px;
			padding-bottom: 50px; } }
	&__title {
		margin-bottom: 50px;
		@include sm {
			margin-bottom: 30px; } }
	&__content {
		margin: 0 -15px;
		@include clr; }
	&__item {
		float: left;
		padding: 0 15px;
		width: 33.33%;
		@include sm {
			width: 100%; } } }

.list {
	&__link {
		display: block;
		padding: 11px 0;
		color: $dark;
		font-family: $montserrat;
		@include f(14,1.2,2.1);
		text-transform: uppercase;
		transition: color 0.3s;
		@include lg {
			font-size: 13px; }
		@include sm {
			font-size: 12px; }
		@include hover {
			color: $green; } } }
